import styled, {css} from 'styled-components';
import {
  Span,
  H2,
  H3
} from '../..//components/globals/index';
import{BaseDropdown} from '../..//components/dropdown/index';

export const FaqAnswer = styled(BaseDropdown)`
-webkit-transition: max-height 1.5s ease-out;
  transition: max-height 0.8s ease-out;
`;
export const TitleWrapper = styled.li`
  border-color: rgba(112, 112, 112, 0.4);
  border-style: solid;
  border-width: 0px 1px 1px 1px;
  padding: 1vw;
  ${props => props.selected  && css`
    background-color: #e75a50;
    `
  }
  ${props => props.overlay && css`
        padding: 1em 0 1em 0;
    `}
`;

export const FaqSpan = styled(Span)`
  font-size: 1.5rem;
  @media screen and (max-width:800px){
    font-size: 4.5vw;
    color: white;
    transition: 0.3s;
    padding: 8px;
  }
`;
export const QnAh2 = styled(H2)`
  font-size: 2vw;
  line-height: 4vw;
  padding: 2vw;
  @media screen and (min-width:800px){
    font-size: 1rem;
    line-height: 2rem;
  }
`
export const FaqH3 = styled(H3)`
  font-size: 1.2em;
`
export const FaqWrapper = styled.div`
  display: flex;
  position: relative;
  @media screen and (min-width: 1200px){
    margin-left: 12%;
    margin-right: 10%;
  }
  @media screen and (max-width: 800px){
    display: flex;
    flex-direction: column;
  }
`
export const MenuWrapper = styled.div`
  width: -webkit-fill-available;
  margin: 0;
  padding: 0;
  background-color: #e75a50;
  color: white;
  font-weight: 600;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  @media screen and (min-width: 800px){
    display: none;
    height: 0;
  }
  ${props => props.sticky && css`
    position: fixed;
    top: 0;
    width: 100%;`
  }
`
export const FaqOverlay = styled.div`
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
`
export const OverlayBlank = styled.div`
  background-color: rgba(178,178,178,0.1);
  height: -webkit-fill-available;
`
export const TitlesWrapper = styled.ul`
  width: 35%;
  height: fit-content;
  margin-top: 10px;
  margin-left: 16px;
  border-color: rgba(112, 112, 112, 0.4);
  border-style: solid;
  border-width: 1px 0px 0px 0px;
  @media screen and (max-width: 800px) {
    display: none;
  }
  @media screen and (min-width: 1200px) {
    margin-left: 0;
  }
`
export const QnaWrapper = styled.div`
  width: 55%;
  margin-left: 5%;
  margin-right: 5%;
  @media screen and (max-width: 800px){
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
`
export const Qna = styled.div`
  border-color: rgba(112, 112, 112, 0.4);
  border-style: solid;
  border-width: 1px;
  margin-bottom: 5px;
  margin-top: 10px;
`
export const QnaQ = styled.div`
  padding-top: 1em;
  padding-bottom: 1em;
`



