import React, {Component, ReactDOM} from 'react';
import styled from 'styled-components';
import {
  FaqAnswer,
  FaqSpan,
  TitleWrapper,
  QnAh2,
  FaqH3,
  FaqWrapper,
  MenuWrapper,
  FaqOverlay,
  OverlayBlank,
  TitlesWrapper,
  QnaWrapper,
  Qna,
  QnaQ,
} from './style';

// import './faqPage.css';
import {titles,QnA1, QnA2} from './TitlesnQA';


class FaqDropdown extends Component {
  constructor(props){
    super(props);
    this.state = {
      closed: true,
    }
    this.dropdownToggler = this.dropdownToggler.bind(this);

  }
  dropdownToggler=() => {
      console.log("clicked");
      const oldState = this.state.closed;
      this.setState({closed: !oldState});
  }
  render(){
    return(
      <Qna>
        <QnaQ onClick={this.dropdownToggler}>
          <FaqH3>{this.props.question}</FaqH3>
        </QnaQ>
        <FaqAnswer maxHeight={"1000px"} collapse={this.state.closed}>
            <QnAh2>{this.props.answer}</QnAh2>
        </FaqAnswer>
      </Qna>
    )
  }
}

const QnAtable = ({qnas}) => {

  return(
    qnas.map((qua,i) =>{
      return(
        <div>
          <FaqDropdown
          question={qua.q}
          answer={qua.a} />
        </div>
      );}
    )
  );
}
class QnAtitle extends Component{
  constructor(props){
    super(props);
    console.log(props);
    this.state={
     currentTitle: titles[0],
     show: "show",
     overlay: this.props.overlay || "off",
     sticky: false,
    }
    this.scrollmenu = React.createRef();
    this.handleScroll = this.handleScroll.bind(this);
  }

    switchTitle = (n) =>{
      this.setState({currentTitle: titles[n]});

    }

    overlayToggle =(event)=>{
      if(this.state.overlay === "off")
        this.setState({overlay: "on"});
      if(this.state.overlay === "on")
        this.setState({overlay: "off"});
    }

    OLstyle =() => {
      if(this.state.overlay === "on")
        return {
          height:'100vh',
        };
      else return {
        height: '0',
      };
    }
    // -------------------------------------------------------
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll(event) {

        if(window.scrollY>76){
          this.setState({
            sticky: true
          });
        }else{
          this.setState({
            sticky: false
          });
        }
    }
    // ------------------------------------------------------

    render() {
      return(
        <FaqWrapper>
        <MenuWrapper ref={this.scrollmenu}  sticky={this.state.sticky} onClick={this.overlayToggle}>
          MENU
        </MenuWrapper>
        <FaqOverlay style ={this.OLstyle()}>
          {titles.map((Title,k) => {
            return(
                <TitleWrapper selected={this.state.currentTitle==titles[k]? true: false}
                 overlay={true}
                 onClick={(event)=>{
                    event.stopPropagation();
                    this.switchTitle(k);
                    this.overlayToggle();}}
                >
                  <FaqSpan>{Title.title}</FaqSpan>
              </TitleWrapper>
            );
          })}
          <OverlayBlank onClick={this.overlayToggle} />
        </FaqOverlay>
          <TitlesWrapper>
            {titles.map((Title,k) => {
              return(
                <TitleWrapper
                  selected={this.state.currentTitle==titles[k]? true :false}
                  onClick={() => this.switchTitle(k)}
                  key={k}
                >
                  <FaqSpan>{Title.title}</FaqSpan>
                </TitleWrapper>
              );
            })}
          </TitlesWrapper>
          <QnaWrapper>
            <QnAtable qnas={this.state.currentTitle.qna} />
          </QnaWrapper>
        </FaqWrapper>
      );
    }
  }
export default QnAtitle;



